import { Component, OnInit, OnChanges } from '@angular/core';
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons';
import { UserService } from 'src/app/services/user.service';
import { ActivatedRoute, Router } from '@angular/router';
import { res } from '../models/res.model';
import { StudentsService } from 'src/app/services/students.service';

@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.css'],
  host: {
    "(window:resize)":"onWindowResize($event)"
  },
})
export class SidenavComponent implements OnInit {
  bars: any;
  show: boolean;
  display:String;
  close: any;
  log: any
  result: any;
  user: any;
  requests: any;


  constructor(private userServe: UserService, private route: ActivatedRoute, private router: Router, private stuServe: StudentsService) {
    this.bars= faBars,
    this.close=faTimes
    this.show=true
    this.userServe.getDetails().subscribe((result:res)=>{
      console.log(result.body,"asdasd");
      this.user = result.body;
    })
  }

  ngOnInit(): void {
    this.stuServe.getRegisteredStudents().subscribe((result:res)=>{
      console.log(result.body);
      this.requests = result.body.length;
    })
  }


  onWindowResize(event){
    if(event.target.innerWidth<690){
      this.show=false;
      this.display="none"
    }
    else{
      this.show=true;
      this.display="block"
    }
  }

  getLeft(){
    if(this.show){
      return '220px'
    }

    return '30px'
  }

  toggleShow(){
    this.show=!this.show;
    if(this.show){
      this.display="block"
    }
    else{
      this.display="none"
    }
  }

  logout(){
    this.userServe.logout();
    this.router.navigate([''])
  }

}
